// import Bmob from '@/utils/bmob.js'
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			classId: '',	// 分类Id
			pageSetting: {
				pageIndex: 1,	// 页码
				pageSize: 10,	// 每页页数
				totalPage: 1,	// 总页数
				totalSize: 0,	// 总条数数
			},
			postList: [],	// 帖子列表
			localId: '',	// 地址的id， 默认为空
		}
	},
	computed: {
	},
	async onLoad(option) {
		let that = this;
		that.classId = option.classId
		//	获取地址
		uni.getStorage({
		    key: 'localId',
		    success: function (res) {
				that.localId = res.data ? res.data : '';
				that.getPostList();
		    }
		});
		uni.setNavigationBarTitle({
		    title: option.title
		});
	},
	onReady(){
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '微撩',
			path: '/pages/index/index'
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.pageSetting.pageIndex = 1;
		this.postList = [];
		this.getPostList();		// 获取帖子列表 
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// 到底
	onReachBottom(){
		if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
			//设置列表数据
			this.getPostList()
		}
	},
	methods: {
		// 详情、结果
		navTo(url){
			uni.navigateTo({ url: url })
		},
		// 获取帖子列表
		getPostList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('postList');
			query.limit(10);	// 每页条数
			query.skip(10 * (that.pageSetting.pageIndex - 1));	// 分页查询// 对score字段降序排列
			query.order("-updatedAt");
			query.equalTo("belongsClass", "==", that.classId);
			query.include("author,belongsClass", "_User,postClass");
			// 如果选择了地址，则关联地址
			if(that.localId){
				query.equalTo("belongsLocal", "==", that.localId);
			}
			query.count().then(res => {
				if(res.count === 0){
					that.postList = []
					uni.hideLoading();
				}else{
					that.pageSetting.totalPage = parseInt(res.count/that.pageSetting.pageSize) + 1
					query.find().then(res => {
						that.pageSetting.pageIndex += 1;
						that.postList = that.postList.concat(res);
						uni.hideLoading();
					});
				}
			});
		},
	}
}
